.pageNavFixed {
  position: fixed;
  z-index: 1500;
  top: 0;
  left: 0;
  left: -230px;
  width: 230px;
  padding: 30px;
  box-sizing: border-box;
  border-radius: 0px 0px 30px 0px;
  background-color: #fff;
  transition: 0.5s;
  border-right: 2px solid black;
  border-bottom: 2px solid black;
}

.pageNavFixed:hover {
  left: 0px;
}

.pageNavFixed::after {
  content: 'NAV';
  position: absolute;
  width: 40px;
  height: 20px;
  top: 0;
  padding-top: 0;
  right: -40px;
  border-radius: 0px 0px 5px 0px;
  background-color: black;
  color: #7EB325;
  padding: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 10px;
  box-sizing: border-box;
  cursor: pointer;
}

.pageNavFixed__cont {
  width: 100%;
  max-height: 600px;
  overflow-x: hidden;
  overflow-y: auto;
}

.pageNavFixed__cont a {
  display: block;
  font-size: 16px;
  line-height: 20px;
  color: #7EB325;
  margin-bottom: 10px;
}

.pageNavFixed__cont a:hover {
  text-decoration: underline;
}

.pageNavFixed__cont a.completed {
  color: green;
}

.pageNavFixed__cont b {
  display: block;
  margin-bottom: 15px;
}

.pageNavFixed__cont div {
  margin: 0;
  margin-bottom: 50px;
}

.blur {
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  position: fixed;
  background: rgba(0, 0, 0, 0.24);
  z-index: 1000;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding: 0 20px;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;
  overflow-y: scroll;
}

.blur > {
  cursor: pointer !important;
}

.blur.active {
  opacity: 1;
  visibility: visible;
}

.popup {
  opacity: 0;
  transition: 0.2s;
  visibility: hidden;
  transition: 0.2s;
  display: flex;
  flex-direction: column;
}

.popup.active {
  opacity: 1;
  visibility: visible;
}

.popup img:first-child {
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(-50%, -50%);
}

.popup img:nth-child(2) {
  z-index: -1;
  position: absolute;
  bottom: 0;
  right: 0;
  transform: translate(50%, 50%);
}

.modal__close {
  cursor: pointer;
}

.modal__close svg {
  transition: 0.2s;
}

.modal__close:hover svg {
  transform: rotate(90deg);
}

.modal-1 {
